import AutoZindex from "./AutoZindex";
import guid from "./guid";
import Icon from "./Icon";
import Stage from "./Stage";

interface ContainerOption {
    name: any;
    id?: string // 自增或者编辑传入
    x: number
    y: number
    w: number
    h: number
    zindex?: number
}

// 方形容器 内置一个删除icon，点击删除container，icon的显示状态由container的active来控制
class Container {
    parent: Stage;
    type: string;
    children: any[];
    active: boolean;
    zindex: number;
    id: string;
    c: string;
    x: number;
    y: number;
    w: number;
    h: number;
    delIcon: any;
    name: any;
    buildInChildren: any[];
    constructor(option: ContainerOption) {
        this.name = option.name;
        this.x = option.x;
        this.y = option.y;
        this.w = option.w;
        this.h = option.h;
        this.id = option.id ? option.id : guid();
        this.zindex = option.zindex ? option.zindex : AutoZindex.getIndex();
        this.active = false;
        this.children = [];
        this.buildInChildren = []
        this.type = "container";
        
        this.parent = null;
        
        this.delIcon = new Icon({
            offsetX: this.w - 5,
            offsetY: -5,
            w: 10,
            h: 10,
            src: "./close.png",
            zindex: AutoZindex.getHindex()
        })
        this.delIcon.x = this.x + this.delIcon.offsetX
        this.delIcon.y = this.y + this.delIcon.offsetY
        this.delIcon.addEvent("click", (t) => {
            this.destory()
        });
        // 内置元素
        this.buildInChildren.push(this.delIcon)
    }
    add(child) {
        child.parent = this;
        this.children.push(child);
    }
    remove(child) {
        let index = this.parent.children.findIndex((item) => item.id == child.id)
        index != -1 && this.parent.children.splice(
            index,
            1
        );
    }
    destory() {
        this.parent.remove(this)
    }
    draw(ctx) {
        ctx.beginPath();
        ctx.lineWidth = 1;
        // 绘制所有子元素，但是以container为基准，如container在(100, 100)，子元素1在(20, 20)，那么子元素1的绘制位置为(120, 120)
        this.children.forEach((item) => {
            item.updatePosition(this.x, this.y);
            item.draw(ctx);
        });
        
        if (this.active) {
            ctx.beginPath();
            ctx.strokeStyle = "#0f0";
            ctx.strokeRect(this.x - 2, this.y - 2, this.w + 4, this.h + 4);
            
            // 内置了一个删除icon，所以在active的时候绘制内置元素，内置元素有点击事件
            this.buildInChildren.forEach(item => {
                item.draw(ctx)
            })
        }
    }
    setActive(f) {
        this.active = f;
    }
    updatePosition(x, y) {
        this.x = x;
        this.y = y;
        
        this.buildInChildren.forEach(item => {
            item.x = x
            item.y = y
        })
    }
    // 点是否在矩形内
    pointInElement(x, y) {
        return this.x <= x && this.y <= y && this.x + this.w >= x && this.y + this.h >= y;
    }
}

export default Container


