document.getElementById("upload_file2").addEventListener("change", function (e: any) {
    let files = e.target.files
    let config = (<HTMLTextAreaElement>document.getElementById("configinfo")).value;
    config = JSON.parse(config || "[]")
    // 没有配置文件也可以，就当新增了，Fuck，单独新增功能没用了
    for(let i = 0; i < files.length; i++) {
        let reader = new FileReader();
        reader.onloadstart = function () {};
        reader.readAsDataURL(files[i]);
        reader.onloadend = function (e) {
            var base64 = this.result;
            var image: HTMLImageElement = new Image();
            image.src = <string>base64;
            let cur = (<any>config).find(item => item.name == files[i].name)
            // 如果存在，就返显xy，否则初始化为0
            image.onload = function (r) {
                data.push({
                    name: files[i].name,
                    x: cur ? cur.x : 0,
                    y: cur ? cur.y : 0,
                    w: (<any>this).width,
                    h: (<any>this).height,
                    src: base64,
                });
                s2.add(
                    new ImageElm({
                        name: files[i].name,
                        x: cur ? cur.x : 0,
                        y: cur ? cur.y : 0,
                        w: (<any>this).width,
                        h: (<any>this).height,
                        src: <string>base64,
                        parent: s2,
                    })
                );
            };
        };
        reader.onerror = function () {};
    }
})
document.getElementById("export").addEventListener("click", () => {
    let filename = (<HTMLInputElement>document.getElementById("filename")).value;
    let filepath = (<HTMLInputElement>document.getElementById("filepath")).value;
    if (!filename) {
        alert("请输入要生成的文件名称");
        return;
    }
    if (!filepath) {
        alert("请输入文件相对路径");
    }
    var src = s2.canvas.toDataURL("image/png");
    var link = document.createElement("a");
    link.download = filename;
    link.href = src;
    link.dataset.downloadurl = ["image/png", link.download, link.href].join(":");
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);

    let css = "";
    s2.children.forEach((item) => {
        css += `
            .${item.name} {
                width: ${item.w}px;
                height: ${item.h}px;
                background: url(${filepath}${filename}.png) no-repeat -${item.x}px -${item.y}px; 
            }
            </br>
        `;
    });
    document.getElementById("finalcss").innerHTML = css;

    let config = [];
    s2.children.forEach((item) => {
        config.push({
            name: item.name,
            x: item.x,
            y: item.y,
            w: item.w,
            h: item.h,
            type: "ImageElm",
            src: item.src,
        });
    });
    document.getElementById("config").innerHTML = JSON.stringify(config);
});



