import AutoZindex from "./AutoZindex";
import Container from "./Container";
import Icon from "./Icon";
import Stage from "./Stage";

interface ImageElmOption {
    name: string
    x: number
    y: number
    w: number
    h: number
    src: string
    parent: Stage
}

// 一个图片组件，一个图片
class ImageElm {
    x: number;
    y: number;
    icon: Icon;
    container: any;
    c: string;
    h: number;
    w: number;
    parent: Stage;
    image: Icon;
    src: string;
    name: string;
    constructor(option: ImageElmOption) {
        this.name = option.name
        this.x = option.x
        this.y = option.y
        this.w = option.w
        this.h = option.h
        this.src = option.src
        this.parent = option.parent

        this.container = null;
        this.image = null

        this.init();
        return this.container
    }

    init() {
        this.container = new Container({
            name: this.name, // 用来生成信息
            x: this.x,
            y: this.y,
            w: this.w,
            h: this.h,
            zindex: AutoZindex.getHindex()
        });

        this.image = new Icon({
            offsetX: 0,
            offsetY: 0,
            w: this.w,
            h: this.h,
            src: this.src,
        })

        this.container.add(this.image)
    }
}

export default ImageElm


